.main-banner {
  position: relative;
  background: linear-gradient(135deg, #ff6b6b 0%, #ffa726 50%, #ff5722 100%);
  color: white;
  cursor: pointer;
  overflow: hidden;
  margin: 1rem 0;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(255, 107, 107, 0.3);
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 12px 40px rgba(255, 107, 107, 0.4);
  }

  .banner-container {
    position: relative;
    padding: 3rem 2rem;
    max-width: 1200px;
    margin: 0 auto;
    z-index: 2;

    @media (max-width: 768px) {
      padding: 2rem 1.5rem;
    }

    @media (max-width: 480px) {
      padding: 1.5rem 1rem;
    }
  }

  .banner-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 2rem;

    @media (max-width: 768px) {
      flex-direction: column;
      text-align: center;
      gap: 1.5rem;
    }
  }

  .banner-text {
    flex: 1;

    .banner-title {
      font-size: 3rem;
      font-weight: bold;
      margin: 0 0 1rem 0;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
      line-height: 1.2;

      @media (max-width: 768px) {
        font-size: 2.5rem;
      }

      @media (max-width: 480px) {
        font-size: 2rem;
      }
    }

    .banner-subtitle {
      font-size: 1.2rem;
      margin: 0 0 2rem 0;
      opacity: 0.95;
      line-height: 1.6;

      @media (max-width: 768px) {
        font-size: 1.1rem;
        margin-bottom: 1.5rem;
      }

      @media (max-width: 480px) {
        font-size: 1rem;
      }
    }

    .banner-features {
      display: flex;
      gap: 2rem;

      @media (max-width: 768px) {
        justify-content: center;
        gap: 1.5rem;
      }

      @media (max-width: 480px) {
        flex-direction: column;
        gap: 1rem;
        align-items: center;
      }

      .feature-item {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        background: rgba(255, 255, 255, 0.2);
        padding: 0.75rem 1rem;
        border-radius: 25px;
        backdrop-filter: blur(10px);
        transition: all 0.3s ease;

        &:hover {
          background: rgba(255, 255, 255, 0.3);
          transform: translateY(-1px);
        }

        .feature-icon {
          font-size: 1.25rem;
        }

        .feature-text {
          font-size: 0.9rem;
          font-weight: 500;
          white-space: nowrap;
        }
      }
    }
  }

  .banner-action {
    .shop-now-btn {
      display: flex;
      align-items: center;
      gap: 0.75rem;
      background: white;
      color: #ff6b6b;
      border: none;
      padding: 1rem 2rem;
      border-radius: 50px;
      font-size: 1.1rem;
      font-weight: bold;
      cursor: pointer;
      transition: all 0.3s ease;
      box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);

      &:hover {
        background: #f8f8f8;
        transform: translateY(-2px);
        box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
      }

      &:active {
        transform: translateY(0);
      }

      mat-icon {
        font-size: 1.25rem;
        transition: transform 0.3s ease;
      }

      &:hover mat-icon {
        transform: translateX(4px);
      }

      @media (max-width: 480px) {
        padding: 0.875rem 1.5rem;
        font-size: 1rem;
      }
    }
  }

  // Decorative Elements
  .banner-decoration {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    z-index: 1;

    .decoration-element {
      position: absolute;
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.1);
      animation: float 6s ease-in-out infinite;

      &.element-1 {
        width: 120px;
        height: 120px;
        top: 20%;
        right: 10%;
        animation-delay: 0s;
      }

      &.element-2 {
        width: 80px;
        height: 80px;
        top: 60%;
        right: 25%;
        animation-delay: 2s;
      }

      &.element-3 {
        width: 60px;
        height: 60px;
        top: 30%;
        right: 35%;
        animation-delay: 4s;
      }

      @media (max-width: 768px) {
        display: none;
      }
    }
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
    opacity: 0.1;
  }
  50% {
    transform: translateY(-20px) rotate(180deg);
    opacity: 0.2;
  }
}